h1 {
    font-size: 5rem;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
    font-weight: bold;
    color: gray !important;
}

input {
    display: block;
    background-color: transparent !important;
    color: white;
    margin-top: 10%;
    font-size: 3.5rem;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-shadow: none;
    font-weight: bold;
    text-align: center;
    outline: 0;
    border: none;
    width: 80%;
}

input:focus {
    border: none;
    box-shadow: 0 0 0 0;
    outline: 0;
    caret-color: transparent
}

.box {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

#score {
    position: absolute;
    top: 20px;
    left: 50%;
    margin-left: -50px;
    width: 100px;
    height: 100px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: rgb(255, 149, 0);
    font-size: 50px;

}

.erro {
    color: red
}

.ganharscore {
    animation: maisscore 0.5s ease
}

@keyframes maisscore {
    0%{
        transform: scale(1.0);
    }
    20%{
        transform: scale(1.2);
    }
    45%{
        transform: scale(1.2);
    }
    100%{
        transform: scale(1.0);
    }
}