* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(40, 40, 40);
  color: white;
  overflow: hidden;
}

#leaderboardbtn {
  position: absolute;
  top: 60%;
  width: 10px;
  left: calc(50% - 5px);
  
}

#leaderboardbtn:hover {
  cursor: pointer;
}

#btnclose {
  position: absolute;
  top: 1%;
  right: 1%;
  color: orange;
}

