.perfect {
   color: orange;
   position: absolute;
   top: 40%;
   width: 100px;
   left: calc(50% - 140px);
   display: none;
   font-size: 3rem;
   z-index: -1;
   opacity: 0
}

.aparecer0 {
    animation: appear1 1s ease;
    display: inline !important;
}

.aparecer1{
    animation: appear2 1s ease;
    display: inline !important;
}

.aparecer2 {
    animation: appear3 1s ease;
    display: inline !important;
}



@keyframes appear1 {
    0% {
        transform: translateY(10px) ;
        opacity: 0;
    }
    40%{
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    99% {
        transform: translateY(-30px);
        opacity: 0
    }
}

@keyframes appear2 {
    0% {
        transform: translateY(10px) ;
        opacity: 0;
    }
    40%{
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    99% {
        transform: translateY(-30px) ;
        opacity: 0
    }
}

@keyframes appear3 {
    0% {
        transform: translateY(0px);
        opacity: 0;
    }
    40%{
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    99% {
        transform: translateY(-30px);
        opacity: 0
    }
}

.scoreGanho {
    display: none;
    opacity: 1;
    color: orange;
    position: absolute;
    top: 40%;
    left: calc(50% + 80px);
    transform: translate(-50%, -50%);
    font-size: 2.5rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.box {
    display: flex;
    justify-content: center;
}


#timernumber {
   position: absolute;
   font-size: 2rem;
   font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
   margin-top: 100px;
}

#finallyModal {
    position: absolute;
    top:30px;
    left: calc(50% - 250px);
    width: 500px;
    height: 600px;
    background-color: rgb(31, 31, 31);
    box-shadow: rgb(0, 0, 0) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
    text-align: center;
}

span {
    color: rgb(255, 157, 0);
}

.inputNome {
    display: block !important;
    padding: 15px;
    width: 400px;
    font-size: 2.5rem;
    margin-left: calc(50% - 200px);
    background-color: rgb(35, 35, 35) !important;
    box-shadow: inset rgb(0, 0, 0) 0px 1px 4px !important;
    outline: 1 px solid black;
    color: orange;
}

.inputNome:focus {
    background-color: rgb(35, 35, 35) !important;
    box-shadow: inset rgb(0, 0, 0) 0px 1px 4px !important
}

#buttonenviar {
    width: 200px;
    margin-left: calc(50% - 100px);
    border-radius: 20px;
}

#buttonenviar:hover, .hover {
    transform: scale(1.1);
    cursor: pointer;
}